.form {
    display:grid;
    grid-template-columns: auto 1fr auto;
    grid-gap: 1rem;
    align-items: center;
}

.label {
    font-size: 1.2rem;
    margin-bottom: 0.2rem;
    text-transform: uppercase;
}

.input {
    font-size: 1.6rem;
    padding: 0.5rem 2rem;
    line-height: 2.8rem;
    border-radius: 20px;
    border: 1px solid #ddd;
    margin-bottom: 1rem;
}

.button {
    background-color: rgba(0,0,0,0.75);
    color: white;
    padding: 1rem 2rem;
    border: 1px solid rgba(0,0,0,0.75);
    border-radius: 20px;
    font-size: 1.4rem;
    cursor: pointer;
    transition: background-color 250ms;
}

.button:hover {
    background-color: rgba(0,0,0,0.85);
}

.recipe-list {
    margin-top: 4rem;
    display:grid;
    grid-template-rows: auto 1fr auto;
    grid-gap: 1rem;
}

.recipe {
    padding: 2rem 4rem;
    border-radius: 10px;
    box-shadow: 3px 7px 12px 2px rgb(93,93,93);
    margin-bottom: 2rem;
    background-color: #a9a9a95c;
}

.recipe--title {
    margin-bottom: 0.5rem;
    font-size: 2rem;
}

.recipe--image {
    margin: 0 auto;
    display: block;
}

.recipe--favorite {
    float: left;
}